body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  text-align: center;
  background-color: rgb(104, 98, 90);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  color: rgb(5, 26, 5);
  font-style: italic;
  padding: 10px;
  font-size: 45px;
}

h2 {
  color: rgb(71, 20, 20);
  font-style: italic;
  font-size: 35px;
}


p {
  padding: 5px;
  font-size: 15px;
}

